@import url(https://fonts.googleapis.com/css?family=Raleway:400,500);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
body {
  padding:0;
  margin: 0;
  font-family: 'Raleway', Helvetica, sans-serif;
  /*font-family: Roboto, sans-serif;*/
  color: #333;
}

h1 {
  margin:0;
  padding: 0;
}
.App {
  width: 100%;
  display: flex;
}

/* Profile Section Starts Here*/
.App #profile {
  text-align: center;
  width: 400px;
  height: 100vh;
  position: fixed;
  background: #323435;
  color: orangered;
  overflow-y: auto;
}

.App #profile #minimize {
  padding: 40px 20px;
}

.App #profile #minimize header h1 {
  font-weight: lighter;
  color: #fff;
}


.App #profile #minimize main #profilePic {
  padding-top: 10px;
  padding-bottom: 10px;
}

.App #profile main #profilePic img {
  border-radius: 50%;
  width: 150px;
  height: 150px;
}

.App #profile main #about p {
  color: #e4e3e3;
  line-height: 25px;
  font-size: 14px
}

.App #profile main #skills {
  text-align: left;
  color: #fff;
  padding: 20px 0;
  font-size: 13px;
}

.App #profile main #skills h3 {
  font-weight: lighter;
  margin: 7px;
}

.App #profile main #skills #skill span {
  padding: 2px 5px;
  margin: 5px;
  border-radius: 2px;
  display: inline-flex;
}

.App #profile footer {
  display: flex;
  flex-direction: column;
  text-align: left;
  padding: 20px 40px;
}

.App #profile footer a {
  display: flex;
  align-items: center;
  text-decoration: none;
  background: #323435;
  color: orangered;
  font-size: 30px;
  margin-top: 10px;

}

.App #profile footer span {
  font-size: 14px;
  margin-left: 40px;
  color: orangered;
}
/* Profile Section Ends Here*/

/* /////////////////////////////////////////////////////////////   */

/* Projects Section Starts Here*/
.App #projects {
  background: #1d1d1d;
  position: absolute;
  left: 400px;
  right: 0;
  color: #ececec;
}

.App #projects header {
  display: flex;
  justify-content: flex-end;
  position: fixed;
  padding-top: 10px;
  right: 50px;
  z-index: 1;
}

.App #projects header .contacts {
  display: flex;
  align-items: center;
}

.App #projects header .contacts .icons {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 30px;
  padding: 10px 17px;
  border-radius: 50%;
  text-decoration: none;
  background: #0c0b1169;
  margin-left: 20px;
  color: #25b2ba;
}

.App #projects header .contacts .icons:hover {
  -webkit-transition-property: background, color;
  transition-property: background, color;
  -webkit-transition-duration: .5s;
          transition-duration: .5s;
  background: #25b2ba7d;
  color: #ffffff;
}

.App #projects header .contacts .icons span {
  font-size: 9px;
}


.App #projects main nav ul {
  display: flex;
  list-style-type: none;
  font-size: 16px;
  justify-content: center;
  padding: 0;
  margin: 0;
}

.App #projects main nav ul li {
  cursor: pointer;
  padding: 5px 10px;
  margin-left: 20px;
  border-radius: 2px;
  color: #dedddd;
}

.App #projects main nav ul li:hover {
  background: #dedede;
  color: #fff;
  -webkit-transition-property: background, color;
  transition-property: background, color;
  -webkit-transition-duration: 1s;
          transition-duration: 1s;
}

li.active {
  background-color: orangered;
  color: #000 !important;
  -webkit-transition-property: background, color;
  transition-property: background, color;
  -webkit-transition-duration: 2s;
          transition-duration: 2s;
}

.App #projects main #list-header {
  padding: 40px 50px 20px;
}

.App #projects main #list-header p {
  color: #cacacab5;
  padding: 55px 0;
  font-size: 15px;
}


@-webkit-keyframes open { /* The rendering Animation */
  0% { opacity: 0 }
  100% { opacity: 1 }
}


@keyframes open { /* The rendering Animation */
  0% { opacity: 0 }
  100% { opacity: 1 }
}

@-webkit-keyframes close { /* The rendering Animation */
  0% { opacity: 0 }
  100% { opacity: 1 }
}

@keyframes close { /* The rendering Animation */
  0% { opacity: 0 }
  100% { opacity: 1 }
}

.App #projects main .first {
  -webkit-animation: open 1s; /* Safari 4+ */ /* Fx 5+ */ /* Opera 12+ */
  animation:         open 1s; /* IE 10+, Fx 29+ */
}

.App #projects main .second {
  -webkit-animation: close 1s; /* Safari 4+ */ /* Fx 5+ */ /* Opera 12+ */
  animation:         close 1s; /* IE 10+, Fx 29+ */
}

.App #projects main .each-project {
  display: flex;
  align-items: center;
  padding: 50px;
  height: 250px;
  justify-content: space-between;
}


.App #projects main .each-project .project-img {
  position: relative;
  text-decoration: none;
  color: #fff;
  width: 50%;
}

.App #projects main .each-project .project-img span {
  position: absolute;
  padding: 2px 5px;
  margin: 0 0 5px 0;
  border-radius: 0 3px 3px 0;
  box-shadow: 3px 1px 6px black;
  left: -10px;
}

.App #projects main .each-project .project-img img {
  display: block;
  width: 100%;
  height: auto;
  box-shadow: 1px 1px 2px 0px #000000;
}

.App #projects main .each-project .project-info {
  background: #00000008;
  padding: 5px 10px;
  font-size: 14px;
  width: 43%;
  height: 100%;
  position: relative;
  color: rgba(255,255,255,.75);
}

.App #projects main .each-project .project-info h1 {
  font-weight: lighter;
  margin-bottom: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #e4e4e4;
}

.App #projects main .each-project .project-info h1 .dates {
  font-size: 15px;
  padding-right: -20px;
  color: #a48525;
  text-shadow: 0 0 20px black;
}


.App #projects main .each-project .project-info p {
  margin-bottom: 10px;
  line-height: 20px;
  font-size: 13px;
}

.App #projects main .each-project .project-info .btn-container {
  width: 94%;
  position: absolute;
  bottom: 5px;
}

.App #projects main .each-project .project-info .btn-container .buttons {
  display: flex;
  justify-content: space-between;
}

.App #projects main .each-project .project-info .btn-container .buttons a {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 49%;
  height: 50px;
  max-width: 180px;
  font-size: 16px;
  text-decoration: none;
  background: #25b2ba1a;
  color: #ffffff;
  border-radius: 2px;
}


.App #projects main .each-project .project-info .btn-container .buttons a:hover {
  -webkit-transition-property: background, color;
  transition-property: background, color;
  -webkit-transition-duration: .5s;
          transition-duration: .5s;
  background: orangered;
  color: #1d1d1d;
}




.App #projects #footer {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #474848;
  height: 50vh;
}

.App #projects #footer h2 {
  color: orangered;
  font-weight: lighter;
  font-size: 30px;
}

.App #projects #footer a {
  margin-left: 10px;
  text-decoration: none;
  color: orangered;
}

.App #projects #footer a:hover {
  color: orangered;
}


/* Responsiveness Begins Here  -------------------------------------------------   */

@media(max-width: 1100px) {

  .App #projects main .each-project {
    flex-direction: column;
    align-items: unset;
    padding: 140px 60px 60px;
    height: unset;
    justify-content: unset;
    position: relative;
  }

  .App #projects main .each-project .project-img {
    width: unset;
  }

  .App #projects main .each-project .project-info {
    background: #00000008;
    padding: unset;
    width: 100%;
    position: unset;
  }

  .App #projects main .each-project .project-info h1 {
    position: absolute;
    font-size: 30px;
    left: 60px;
    right: 60px;
    top: 60px;
  }

  .App #projects main .each-project .project-info .btn-container {
    width: unset;
    position: unset;
  }

  .App #projects main nav ul {
    font-size: 14px;
    justify-content: space-around;
  }

  .App #projects main nav ul li {
    margin-left: 0;
    padding: 2px 5px;
  }

  .App #projects main .each-project .project-info .text-btn {
    display: flex;
    align-items: center;
    margin-top: 30px;
  }

  .App #projects main .each-project .project-info .text {
    margin-right: 20px;
  }

  .App #projects main .each-project .project-info .btn-container .buttons {
    display: unset;
  }

  .App #projects main .each-project .project-info .btn-container .buttons a {
    margin-top: 10px;
    width: 160px;
  }
}






@media(max-width: 770px) {

  html, body, p, span {
    font-size: 16px !important;
    line-height: 25px !important;
  }


  .App {
    flex-direction: column;
  }

  .App #profile {
    display: flex;
    justify-content: center;
    min-width: unset;
    height: auto;
    width: 100%;
    position: unset;
    overflow-y: unset;
  }
  
  .App #profile h1 {
    line-height: 40px !important;
    padding: 10px 0;
  }


  .App #profile #minimize {
    width: 90%;
    max-width: 300px;
  }

  .App #profile main img {
    width: 70%;
    height: auto;
  }

  .App #profile footer {
    padding: unset;
    margin: 50px auto;
    width: 125px;
  }

  .App #profile footer a {
    font-size: 40px;
    margin-top: 25px;
    color: orangered;
  }

  .App #profile footer a span {
    font-size: 12px;
    margin-left: 25px;
    color: orangered;
  }

/* Profile ends here */

/*/// ///////////////////////////////////////////////////////*/

/* The Projects section begin here*/
  .App #projects {
    position: unset;
  }

  .App #projects header {
    display: none;
  }

  .App #projects main .each-project {
    width: 90%;
    margin: auto;
    padding: 100px 0 40px;
  }

  .App #projects main .each-project .project-info h1 {
    left: 0;
    right: 0;
    top: 40px;
  }

  .App #projects main .each-project .project-info h1 span {
    font-size: 30px !important;
  }

  .App #projects main .each-project .project-info h1 .dates {
    font-size: 12px !important;
    position: absolute;
    top: 30px;
    right: 0;
  }
}


@media(max-width: 500px)  {

  .App #projects main #list-header {
    padding: unset;
    width: 90%;
    margin: 40px auto;
  }

  .App #projects main nav ul {
    font-size: 18px;
    flex-direction: column;
  }

  .App #projects main nav ul li{
    padding: 15px 10px;
  }

  .App #projects main .each-project .project-info .text-btn {
    display: unset;
  }

  .App #projects main .each-project .project-info .text-btn .text {
    margin-top: 30px;
    margin-bottom: 30px;
  }

  .App #projects main .each-project .project-info .btn-container .buttons a {
    margin-top: 16px;
    max-width: unset;
    width: 100%;
    font-size: 16px;
    height: 50px;
  }

}

.example_d {
  color: orangered !important;
  text-transform: uppercase;
  background: #ffffff;
  padding: 12px;
  border: 4px solid orangered !important;
  border-radius: 6px;
  display: inline-block;
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}

.example_d:hover {
  color: orangered !important;
  border-radius: 50px;
  border-color: orangered !important;
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
  cursor: pointer;
}
